import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    getFormularios(ctx, { codigoFormulario, queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/formulario/${codigoFormulario}/listar`, { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
